body {
  margin: 0;
  padding: 0px;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#squares-in-motion {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
}

#sidebar-inputs {
  width: 160px;
  padding-left: 10px;
  
  background-color:#EEEEEE;
  display: flex;
  flex-direction: column;
  
}

label {
  margin-top: 12px;
  margin-bottom: 4px;
}

input {
  margin-right: 16px;
}

button {
  margin-top: 8px;
  margin-right: 8px;
}
